.container {
  margin: auto;
  max-width: 600px;
}

.todoBlock {
  padding:30px;
  margin-top:50px;
  background: #fff;
  border-radius:10px;
  box-shadow: -9px 17px 13px rgb(0 0 0 /16%)
}

/*app 9-3 style 수정*/
.titl{font-size: 12px; color: #4c7f37;}
.list{background-color: #f5f5f5; border: 1px solid #e6e6e6; border-radius: 5px; padding: 13px; display: flex; justify-content: space-between; margin: 5px 0; color: #4c7f37;}
.list span{flex: 1; margin-left: 5px;}
.btn{background: none; border: none; color: #4c7f37;}
form{display: flex; justify-content: space-between; margin-top: 10px;}
form input[type=text]{background: #fff; border: 1px solid #e6e6e6; border-radius: 5px; box-shadow: 0 1px 1px rgba(0, 0, 0 /5%); height: 45px; font-size: 15px; padding: 0 10px; width: 86.5%; box-sizing: border-box; outline: none; }
form input[type=submit]{width: 12%; height: 45px; background: #96d05c; color: #fff; border-radius: 5px; border: none; font-size: 15px;}
form input[type="checkbox"]{border: none;}
.completed{text-decoration: line-through; color: #999;}

/*app 9-4 list파일일때 추가 dragging할때 스타일링 */
.dragging .list{background-color: #edf1e3;}

/*모두 삭제 버튼 추가*/
.title{display: flex; justify-content: space-between;}
.deleteBtn{background: none;font-weight: bold; color: #8aa97e; border: none; font-size: 16px; cursor: pointer;}